@use "base/variables" as v;
@use "base/mixins" as m;

.main-services {
  position: relative;
  .main-services-grid {
    display: grid;
    grid-template-columns: 1fr;
    margin: 1rem;
    @include m.mobile {
      margin: 6rem 4rem 2rem 4rem;
      gap: 2rem;
    }
    @include m.tablet{
      grid-template-columns: repeat(2, 1fr);
      gap: 2rem;
      margin: 2rem 5% 1rem 5%;
    }
    @include m.laptop {
      grid-template-columns: repeat(4, 1fr);
      gap: 1rem;
      margin: 2rem;
      margin-bottom: 10rem;
    }
    @include m.desktop {
      gap: 4rem;
      margin: 5rem;
      margin-bottom: 10rem;
    }
    @include m.desktopXL {
      gap: 5rem;
      margin: 8rem;
    }
    .service {
      width: 15rem;
      height: 15rem;
      display: flex;
      justify-content: center;
      align-items: center;
      @include m.mobile {
        width: 20rem;
        height: 20rem;
      }
      @include m.laptop {
        width: 15rem;
        height: 15rem;
      }
      @include m.desktop {
        width: 20rem;
        height: 20rem;
        // height: 60rem;
      }
      @include m.desktopXL {
        // height: 60rem;
        width: 23rem;
        height: 23rem;
      }
      background-color: v.$serviceBackground;
      margin: auto;
      margin-bottom: 5rem;
      @include m.laptop {
        margin-bottom: auto;
      }
      position: relative;
      .img-container {
        img {
          width: 100%;
        }
      }
      .img-container:not(.container-beauty) {
        // margin-top: 3rem;
        padding-bottom: 0;
        width: 12rem;
        @include m.mobile {
          // height: 6rem;
          width: 15rem;
          padding-bottom: 2rem;
        }
        @include m.laptop {
          width: 80%;
          // margin-top: 10rem;
          // margin-bottom: 5rem;
        }
        @include m.desktop {
          // height: 8rem;
        }
      }
      .img-container.container-beauty {
        // margin-top: 5rem;
        // margin-bottom: 0;
        padding-bottom: 0;
        width: 12rem;
        @include m.mobile {
          width: 15rem;
          // height: 5rem;
          // margin-top: 4rem;
        }
        @include m.laptop {
          width: 80%;
          // margin-top: 10rem;
          // margin-bottom: 5rem;
        }
        @include m.desktop {
          // height: 8rem;
        }
      }
      button {
        border: none;
        // font-family: v.$title;
        // font-size: 1.5rem;
        background-color: transparent;
        // color: v.$white;
        text-align: center;
        @include m.mobile {
          font-size: 1.5rem;
        }
        @include m.laptop {
          padding: 0;
        }
      }
      .service-btn {
        position: absolute;
        bottom: -1.75rem;
        // font-family: v.$title;
        font-size: 1.5rem;
        background-color: v.$white;
        color: v.$primaryBlue;
        text-align: center;
        border-radius: 3rem;
        margin: auto 5rem;
        width: 10rem;
        padding: 0.5rem 0;
        @include m.mobile {
          bottom: -2rem;
          width: unset;
          padding: 1rem 2rem;
        }
        @include m.laptop {
          width: 80%;
          // padding: 1rem 5rem;
          // font-size: 1.75rem;
          border-radius: 3rem;
        }
        @include m.desktop {
          // padding: 1rem 8rem;
          // font-size: 2.5rem;
          border-radius: 3rem;
        }
        transition: all 0.5s;
        &:hover {
          background-color: lighten($color: v.$blueBtn, $amount: 30);
          transition: all 0.5s;
        }
        button {
          color: v.$primaryBlueDark;
        }
      }
      .service-btn-odd {
        background-color: v.$pinkBtn;
        color: darken($color: v.$pinkBtn, $amount: 20);
        transition: all 0.5s;
        button {
          color: v.$pinkTextBtn;
        }
        &:hover {
          background-color: darken($color: v.$pinkBtn, $amount: 5);
          transition: all 0.5s;
        }
      }
    }
  }
  background-color: v.$blueGridDark;
  padding-top: 3rem;
  @include m.mobile {
    padding-bottom: 3rem;
  }
  @include m.laptop {
    background-color: transparent;
  }
  .service-bottom {
    bottom: 7rem;
    background-color: v.$blueGridDark;
    width: 100%;
    height: 16rem;
    position: absolute;
    z-index: -1;
    @include m.desktopXL {
      bottom: 2rem;
      height: 25rem;
    }
  }
}
