@use "base/variables" as v;
@use "base/mixins" as m;

.esthetics-title {
  position: relative;
  display: flex;
  justify-content: center;
  h2 {
    font-family: v.$title;
    position: absolute;
    text-transform: uppercase;
    color: v.$primaryBlueDark;
    font-size: 3rem;
    top: -2rem;
    text-align: center;
    @include m.tablet {
      font-size: 4rem;
      top: -2.5rem;
    }
    @include m.desktop {
      font-size: 8rem;
      top: -5rem;
    }
    @include m.desktopXL {
      font-size: 10rem;
      top: -6rem;
    }
  }
}

.esthetics-description {
  // background-color: v.$grayBackground;
  .esthetics-description-grid {
    display: grid;
    gap: 1rem;
    @include m.laptop {
      grid-template-columns: 1fr 3fr;
      gap: 1rem;
    }
    .esthetics-description-logo {
      display: flex;
      align-items: center;
      width: 17rem;
      margin: auto;
      @include m.tablet {
        width: 30rem;
      }
      img {
        width: 80%;
        margin: auto;
      }
      @include m.laptop {
        background-color: v.$white;
        margin-left: 2rem;
        margin-top: 2rem;
        margin-bottom: 2rem;
        height: auto;
        border-radius: 7rem 0 0 0;
      }
    }
    .esthetics-description-text {
      display: flex;
      align-items: center;
      font-family: v.$text;
      padding: 1rem;
      @include m.tablet {
        padding: 2rem;
      }
      p {
        text-align: center;
        // font-size: 2rem;
        line-height: 1.5;
        @include m.mobile {
          line-height: unset;
        }
        @include m.tablet {
          text-align: justify;
        }
        @include m.laptop {
          margin-top: 3rem;
          margin-bottom: 3rem;
          margin-right: 1rem;
        }
      }
    }
    @include m.laptop {
      background-color: v.$grayBackground;
      border-radius: 7rem 0 7rem 0;
    }
  }
  padding: 5rem 1.5rem;
  @include m.mobile {
    padding: 7rem 5rem;
  }
  @include m.tablet {
    padding: 7rem 7rem;
  }
  @include m.desktop {
    margin: 7rem 6rem 4rem 6rem;
  }
  @include m.desktopXL {
    margin: 12rem 15rem;
  }
  p {
    color: v.$secondaryGray;
    margin: 0;
    font-size: 1.5rem;
    line-height: 3rem;
    @include m.desktop {
      font-size: 2rem;
      line-height: 5rem;
    }
    @include m.desktopXL {
      font-size: 2.5rem;
      line-height: 5rem;
    }
    text-align: center;
  }
}

.esthetics-options {
  margin: -2rem 2rem 10rem 2rem;
  .esthetics-options-grid {
    display: grid;
    grid-template-columns: 1fr;
    .esthetics-options-img {
      width: 100%;
      img {
        width: 100%;
      }
    }
    .esthetics-options-bg {
      width: 100%;
      height: 3rem;
      background-color: v.$primaryBlueDark;
      margin-top: 1rem;
      position: relative;
      .esthetics-title {
        position: absolute;
        width: 100%;
        bottom: 0.5rem;
        h2 {
          line-height: 1.5;
          color: v.$white;
          font-size: 1.75rem;
          span {
            color: v.$primaryBlueDark;
            margin-top: 1rem;
          }
          @include m.laptop {
            font-size: 2rem;
          }
          @include m.desktop {
            font-size: 2.75rem;
          }
          @include m.desktopXL {
            font-size: 3.5rem;
          }
        }
        @include m.desktop {
          bottom: -1rem;
        }
      }
      @include m.desktop {
        height: 4rem;
      }
      @include m.desktop {
        height: 5rem;
      }
    }
    .esthetics-options-treatments {
      margin-top: 5rem;
      .esthetics-options-bg {
        background-color: v.$secondaryBlue;
      }
      @include m.tablet {
        margin-top: 0;
      }
    }
    @include m.mobile {
      margin-right: 5rem;
      margin-left: 5rem;
    }
    @include m.tablet {
      grid-template-columns: repeat(2, 1fr);
      margin-left: 10%;
      margin-right: 10%;
      gap: 5rem;
    }
    @include m.laptop {
      margin-top: 5rem;
      margin-bottom: 5rem;
      margin-left: 15%;
      margin-right: 15%;
      gap: 10rem;
    }
    & > * {
      transition: transform 0.5s;
      &:hover {
        transform: scale(1.05);
        transition: transform 0.5s;
      }
    }
  }
  @include m.desktop {
    margin-bottom: 20rem;
  }
}

.esthetics-elements {
  .esthetics-element-odd {
    .esthetics-element-title {
      background-color: v.$secondaryBlue;
      padding: 2.5rem 5rem 2.5rem 0;
      text-align: end;
      border-radius: 0 5rem 5rem 0;
    }
    .esthetics-element-description {
      // ul {
      //   li {
      //     list-style: disc;
      //   }
      // }
      border-radius: 0 0 5rem 0;
      padding: 7rem 3rem 4rem 1rem;
      @include m.laptop {
        // border-radius: 5rem;
        margin-left: 10%;
        padding: 7rem 15rem 4rem 0;
      }
      @include m.desktop {
        padding: 12rem 3rem 10rem 1rem;
      }
    }
    .esthetics-element-image {
      right: -17rem;
      @include m.laptop {
        right: -25rem;
      }
      @include m.laptop {
        right: -25rem;
      }
    }
  }
  //  esthetics-element-short

  .esthetics-element-odd:not(.esthetics-element-short),
  .esthetics-element-pair:not(.esthetics-element-short) {
    margin-bottom: 30rem;
    @include m.tablet {
      margin-bottom: 45rem;
    }
    @include m.desktop {
      margin-bottom: 60rem;
    }
  }

  .esthetics-element-short {
    margin-bottom: 20rem;
    @include m.tablet {
      margin-bottom: 30rem;
    }
    @include m.laptop {
      margin-bottom: 40rem;
    }
    @include m.desktop {
      margin-bottom: 45rem;
    }
    .esthetics-element-description {
      // @include m.desktop {
      //   padding: 15rem 3rem 1rem;
      // }
    }
  }

  .esthetics-element-long {
    margin-bottom: 45rem !important;
    @include m.tablet {
      margin-bottom: 52.5rem !important;
    }
    @include m.laptop {
      margin-bottom: 60rem !important;
    }
    @include m.desktop {
      margin-bottom: 70rem !important;
    }
  }

  .esthetics-element-title {
    font-size: 1.5rem;
    color: v.$white;
    width: 90%;
    position: relative;
    @include m.mobile {
      font-size: 1.75rem;
    }
    @include m.tablet {
      font-size: 1.8rem;
      width: 60%;
    }
    @include m.laptop {
      font-size: 2.5rem;
      width: 60%;
    }
    @include m.desktop {
      font-size: 3rem;
      width: 50%;
    }
    span {
      display: none;
      @include m.tablet {
        display: inline;
      }
    }
  }

  .esthetics-element-description {
    background-color: v.$grayBackground;
    margin-left: 0;
    position: absolute;
    left: 0;
    top: 3.5rem;
    width: 100%;
    z-index: -1;
    padding: 5rem 3rem 1rem;
    text-align: center;
    color: v.$secondaryGray;
    font-size: 1.5rem;
    line-height: 2;
    display: flex;
    flex-direction: column;
    justify-content: center;
    @include m.tablet {
      // left: 2.5rem;
      // height: 40rem;
      // width: 40rem;
      font-size: 2rem;
      // padding: 7rem 3rem 4rem 1rem;
      border-radius: 5rem;
    }
    @include m.laptop {
      // width: 98%;
    }
    @include m.desktop {
      // padding: 8rem 3rem 1rem;
    }
    // ul {
    //   list-style-position: inside!important;
    //   list-style: disc!important;
    // }
  }

  .esthetics-element-image {
    position: absolute;
    bottom: -10rem;
    width: 20rem;
    @include m.laptop {
      width: 30rem;
    }
    img {
      width: 100%;
      display: none;
      @include m.tablet {
        display: block;
      }
    }
  }

  .esthetics-element-odd {
    .esthetics-element-price {
      display: none;
      @include m.tablet {
        display: block;
      }
      position: absolute;
      width: 11.5rem;
      height: 12.5rem;
      right: -14rem;
      bottom: 7rem;
      z-index: -1;
      border: 1px solid v.$primaryBlueDark;
      border-radius: 50% 50% 0 0;
      .esthetics-element-price-text {
        padding-top: 1rem;
        color: v.$primaryBlueDark;
        font-size: 2.5rem;
      }
      .esthetics-element-price-extra {
        font-size: 1.2rem;
        color: v.$secondaryGray;
      }
      @include m.laptop {
        width: 13rem;
        height: 13rem;
        bottom: 18rem;
        right: -17rem;
        .esthetics-element-price-text {
          font-size: 3.5rem;
        }
      }
      @include m.desktop{
        width: 15rem;
        height: 15rem;
        bottom: 17.5rem;
        right: -17.5rem;
        .esthetics-element-price-text {
          font-size: 3.5rem;
        }
        .esthetics-element-price-extra{
          font-size: 2rem;
        }
      }
    }
  }

  .special-text-esthetics {
      padding-top: 1.5rem;
  }
  
  .special-price-esthetics {
    padding-top: 0!important;
  }

  .esthetics-element-pair {
    .esthetics-element-price {
      display: none;
      @include m.tablet {
        display: block;
      }
      position: absolute;
      width: 11.5rem;
      height: 12.5rem;
      left: -14rem;
      bottom: 7rem;
      z-index: -1;
      border: 1px solid v.$primaryBlueDark;
      border-radius: 50% 50% 0 0;
      .esthetics-element-price-text {
        padding-top: 1rem;
        color: v.$primaryBlueDark;
        font-size: 2.5rem;
      }
      .esthetics-element-price-extra {
        font-size: 1.2rem;
        color: v.$secondaryGray;
      }
      @include m.laptop {
        width: 13rem;
        height: 13rem;
        bottom: 18rem;
        left: -17rem;
        .esthetics-element-price-text {
          font-size: 3.5rem;
        }
      }
      @include m.desktop{
        width: 15rem;
        height: 15rem;
        bottom: 17.5rem;
        left: -18.5rem;
        .esthetics-element-price-text {
          font-size: 3.5rem;
        }
        .esthetics-element-price-extra{
          font-size: 2rem;
        }
      }
    }
  }

  .esthetics-element-pair {
    .esthetics-element-title {
      background-color: v.$primaryBlueDark;
      padding: 2.5rem 0 2.5rem 5rem;
      text-align: start;
      border-radius: 5rem 0 0 5rem;
      margin-left: 10%;
      @include m.tablet {
        margin-left: 40%;
      }
      @include m.laptop {
        margin-left: 40%;
      }
      @include m.desktop {
        margin-left: 50%;
      }
    }
    .esthetics-element-description {
      border-radius: 0 0 0 5rem;
      padding: 7rem 1rem 4rem 3rem;
      @include m.laptop {
        // border-radius: 5rem;
        position: absolute;
        padding: 7rem 0 4rem 15rem;
        left: unset;
        right: 10%;
      }
      @include m.desktop {
        padding: 12rem 3rem 10rem 1rem;
      }
    }
    .esthetics-element-image {
      left: -17rem;
      @include m.laptop {
        left: -25rem;
      }
      @include m.desktop {
        left: -25rem;
      }
      // @include m.laptop {
      //   display: none; //////////////////////
      // }
    }
  }
  margin-top: 5rem;
  margin-bottom: 20rem;
  @include m.tablet {
    margin-top: 10rem;
    margin-bottom: 35rem;
  }
  @include m.desktop {
    margin-bottom: 50rem;
    margin-top: 20rem;
  }
}

.esthetics-pack {
  .esthetics-pack-grid {
    margin: 5rem 15%;
    display: grid;
    grid-template-columns: 1fr;
    gap: 3rem;
    & > * {
      border-radius: 3rem 3rem 0 0;
      .esthetics-pack-title {
        text-align: center;
        padding-top: 1.5rem;
        padding-bottom: 0.5rem;
        color: v.$white;
        padding-left: 1rem;
        padding-right: 1rem;
        @include m.tablet {
          height: 7rem;
          display: flex;
          align-items: center;
          justify-content: center;
        }
      }
      .esthetics-pack-price {
        text-align: center;
        padding-top: 2rem;
        p {
          font-size: 2.5rem;
          margin: 0;
        }
      }
      .esthetics-pack-text {
        text-align: center;
        p {
          span {
            display: block;
            font-size: 0.8em;
            color: v.$secondaryGray;
          }
        }
      }
      .esthetics-pack-extra {
        text-align: center;
        padding: 1rem;
        color: v.$secondaryGray;
      }
    }
    .esthetics-pack-element-odd {
      border: 1px solid v.$primaryBlueDark;
      .esthetics-pack-title {
        border-radius: 3rem 3rem 0 0;
        background-color: v.$primaryBlueDark;
      }
      .esthetics-pack-price {
        color: v.$primaryBlueDark;
      }
    }
    .esthetics-pack-element-even {
      border: 1px solid v.$secondaryBlue;
      .esthetics-pack-title {
        border-radius: 3rem 3rem 0 0;
        background-color: v.$secondaryBlue;
      }
      .esthetics-pack-price {
        color: v.$secondaryBlue;
      }
    }
    @include m.mobile {
      margin: 5rem 20%;
    }
    @include m.tablet {
      margin: 5rem 5%;
      grid-template-columns: repeat(2, 1fr);
    }
    @include m.laptop {
      margin: 10rem 15%;
      gap: 5rem;
    }
    @include m.laptop {
      margin: 15rem 17%;
      gap: 7rem;
    }
    @include m.desktop {
      margin: 15rem 20%;
      gap: 8.5rem;
    }
    @include m.desktopXL {
      margin: 15rem 27%;
      gap: 10rem;
    }
  }
}
