@use "base/variables" as v;
@use "base/mixins" as m;

.fertility-title {
  position: relative;
  display: flex;
  justify-content: center;
  h2 {
    font-family: v.$title;
    position: absolute;
    text-transform: uppercase;
    color: v.$white;
    font-size: 3rem;
    top: -2rem;
    text-align: center;
    @include m.tablet {
      font-size: 4rem;
      top: -2.5rem;
    }
    @include m.desktop {
      font-size: 8rem;
      top: -5rem;
    }
    @include m.desktopXL {
      font-size: 10rem;
      top: -6rem;
    }
  }
}

.fertility-description {
  height: 40rem;
  padding: 5rem 2rem;
  @include m.mobile {
    padding: 7rem 5rem;
  }
  @include m.tablet {
    padding: 7rem 7rem;
  }
  @include m.desktop {
    padding: 12rem 15rem;
  }
  @include m.desktopXL {
    padding: 20rem 15rem;
  }
  background-color: v.$primaryBlueDark;
  p {
    color: v.$white;
    margin: 0;
    // font-size: 1.5rem;
    line-height: 3rem;
    @include m.desktop {
      // font-size: 2rem;
      line-height: 5rem;
    }
    @include m.desktopXL {
      // font-size: 2.5rem;
      line-height: 5rem;
    }
    text-align: center;
    font-family: v.$text;
  }
}

.fertility-fertihaus {
  background-color: v.$pinkText;
  display: grid;
  grid-template-rows: 1fr 2fr;
  margin: auto;
  border-radius: 0;
  height: 32.5rem;
  .fertihaus-logo {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 2.5rem 3rem 2rem 3rem;
    img {
      width: 100%;
    }
    @include m.mobile {
      padding: 4rem 7.5rem 2rem 7.5rem;
    }
    @include m.tablet {
      border-bottom: 2px solid v.$white;
      padding: 5rem 6rem;
    }
    @include m.laptop {
      border-bottom: none;
      border-right: 2px solid v.$white;
      padding: 0 3.5rem 0 3.5rem;
    }
  }
  @include m.mobile {
    height: auto;
  }
  @include m.tablet {
    width: 85%;
    border-radius: 0 10rem 0 0;
    grid-template-rows: unset;
    margin-bottom: 10rem;
  }
  @include m.laptop {
    grid-template-columns: 1fr 2fr;
    width: 80%;
  }
  @include m.desktopXL {
    width: 70%;
  }
  .fertihaus-info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    p {
      color: v.$white;
      // font-size: 2rem;
      padding: 0 1.5rem 2rem 1.5rem;
      text-align: center;
      @include m.mobile {
        padding: 0 5rem 2rem 5rem;
      }
      @include m.tablet {
        padding: 5rem 5rem 2rem 5rem;
      }
    }
    a {
      background-color: v.$pinkBtn;
      text-decoration: none;
      color: v.$pinkText;
      // font-size: 3rem;
      padding: 1rem 1.5rem;
      border-radius: 1.75rem;
      height: fit-content;
      @include m.mobile {
        padding: 1rem 3.5rem;
        margin-bottom: 4rem;
      }
      @include m.tablet {
        margin-bottom: 5rem;
      }
      transition: all 0.5s;
      &:hover {
        background-color: darken($color: v.$pinkBtn, $amount: 10);
        transition: all 0.5s;
      }
    }
  }
}

.fertility {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  a {
    background-color: v.$pinkBtn;
    text-decoration: none;
    color: v.$pinkText;
    // font-size: 3rem;
    margin-top: 2.5rem;
    margin-bottom: 4rem;
    padding: 1rem 1.5rem;
    border-radius: 1.75rem;
    height: fit-content;
    @include m.mobile {
      padding: 1rem 3.5rem;
      margin-top: 0;
      margin-bottom: 5rem;
    }
    @include m.tablet {
      margin-top: 2rem;
    }
    @include m.desktop {
      margin-top: -5rem;
    }
    transition: all 0.5s;
    &:hover {
      background-color: darken($color: v.$pinkBtn, $amount: 10);
      transition: all 0.5s;
    }
  }
}
