@use "base/variables" as v;
@use "base/mixins" as m;

.directory-title {
  position: relative;
  display: flex;
  justify-content: center;
  h2 {
    font-family: v.$title;
    position: absolute;
    text-transform: uppercase;
    color: v.$primaryBlueDark;
    font-size: 3rem;
    top: -2rem;
    text-align: center;
    @include m.tablet {
      font-size: 4rem;
      top: -2.5rem;
    }
    @include m.desktop {
      font-size: 8rem;
      top: -5rem;
    }
    @include m.desktopXL {
      font-size: 10rem;
      top: -6rem;
    }
  }
}

.pink-btn {
  a {
    background-color: v.$pinkBtn;
    color: v.$pinkText;
    transition: all 0.5s;
    &:hover {
      background-color: darken($color: v.$pinkBtn, $amount: 5);
      transition: all 0.5s;
    }
  }
}

.directory-grid {
  margin: 10rem 1rem 6rem 1rem;
  display: grid;
  grid-template-columns: 1fr;
  gap: 3rem;
  @include m.mobile {
    margin: 7rem 4rem;
  }
  @include m.tablet {
    margin: 5rem 2rem;
    grid-template-columns: 1fr 1fr;
  }
  @include m.desktop {
    grid-template-columns: 1fr;
    margin: 10rem auto;
    gap: 5rem;
    width: 90%;
  }
  @include m.desktopXL {
    width: 70%;
  }
  .directory-text {
    .directory-text-align {
      display: flex;
      align-items: center;
    }
    display: grid;
    padding: 2rem 1.5rem;
    @include m.tablet {
      grid-template-rows: 3fr 1fr;
      padding: 2rem 2rem;
    }
    @include m.laptop {
      grid-template-rows: 3fr 1fr;
      padding: 2rem 2.5rem;
    }
    @include m.desktop {
      padding: 2rem 7.5rem;
    }
    @include m.desktopXL {
      grid-template-rows: 3fr 2fr;
    }
    p {
      vertical-align: middle;
      font-size: 1.25rem;
      color: v.$secondaryGray;
      text-align: center;
      @include m.laptop {
        font-size: 1.75rem;
      }
    }
    .directory-btn {
      display: flex;
      justify-content: center;
      align-items: flex-end;
      @include m.desktopXL {
        align-items: flex-start;
      }
    }
    a {
      font-family: v.$textNormal;
      color: v.$white;
      text-decoration: none;
      background-color: v.$primaryBlue;
      padding: 1rem 1.5rem;
      border-radius: 1.75rem;
      height: fit-content;
      @include m.mobile {
        padding: 1rem 3.5rem;
      }
      transition: all 0.5s;
      &:hover {
        background-color: darken($color: v.$blueBtn, $amount: 10);
        transition: all 0.5s;
      }
    }
  }
  .directory-logo {
    background-color: v.$primaryBlueDark;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: v.$white;
    font-family: v.$text;
    text-align: center;
    font-size: 1.25rem;
    padding: 1rem 0;
    @include m.tablet {
      font-size: 1.7rem;
      padding: 1.5rem 0;
    }
    @include m.desktop {
      font-size: 2rem;
      padding: 1.5rem 0;
    }
    @include m.desktopXL {
      font-size: 3rem;
      padding: 1.5rem 0;
    }
    .img-container {
      height: 6rem;
      margin-top: 1.5rem;
      margin-bottom: 1.5rem;
      @include m.laptop {
        height: 10rem;
        margin-top: 3rem;
        margin-bottom: 2.5rem;
      }
      img {
        height: 100%;
      }
    }
    p {
      @include m.mobile {
        padding-left: 1rem;
        padding-right: 1rem;
      }
      @include m.desktopXL {
        padding-left: 2rem;
        padding-right: 2rem;
      }
    }
  }
  .directory-text {
    background-color: v.$grayBackground;
  }
  .directory-odd {
    display: grid;
    grid-template-rows: 1fr 2fr;
    @include m.mobile {
      grid-template-rows: 2fr 3fr;
    }
    @include m.desktop {
      grid-template-columns: 1fr 4fr;
      grid-template-rows: 1fr;
      gap: 3rem;
    }
    .directory-logo {
      border-radius: 5rem 5rem 0 0;
      @include m.desktop {
        border-radius: 10rem 0 0 0;
      }
    }
    .directory-text {
      border-radius: 0 0 0 0;
      @include m.desktop {
        border-radius: 0 0 10rem 0;
      }
    }
  }
  .directory-even {
    display: grid;
    grid-template-rows: 1fr 2fr;
    @include m.mobile {
      grid-template-rows: 2fr 3fr;
    }
    @include m.desktop {
      grid-template-columns: 4fr 1fr;
      grid-template-rows: 1fr;
      gap: 3rem;
    }
    .directory-logo {
      order: -1;
      border-radius: 5rem 5rem 0 0;
      @include m.desktop {
        order: 0;
        border-radius: 0 10rem 0 0;
      }
    }
    .directory-text {
      border-radius: 0 0 0 0;
      @include m.desktop {
        border-radius: 0 0 0 10rem;
      }
    }
  }
}

.service {
  margin-top: 2rem;
  margin-bottom: 2rem;
  // margin: 5rem;
  // height: 150vw;
  @include m.tablet {
    margin-top: 8rem;
    margin-bottom: 5rem;
    // height: 15rem;
    // margin-top: 5rem;
  }
  @include m.laptop {
    // height: 20rem;
    margin-top: 15rem;
    margin-bottom: 10rem;
  }
  @include m.desktop {
    // height: 30rem;
    margin-top: 20rem;
  }
  .service-head {
    // position: relative;
    h2 {
      text-transform: uppercase;
      // position: absolute;
      // top: 1rem;
      color: v.$primaryBlueDark;
      font-size: 2rem;
      text-align: center;
      width: 100%;
      @include m.tablet {
        width: 70%;
        font-size: 3rem;
        margin-left: 4.5rem;
        text-align: left;
      }
      @include m.laptop {
        width: 60%;
        margin-left: 12.5rem;
      }
      @include m.desktop {
        width: 60%;
        margin-left: 12.5rem;
        font-size: 4.5rem;
      }
      @include m.desktopXL {
        margin-left: 20%;
        font-size: 5rem;
      }
      .head-1 {
        position: relative;
        .head-2 {
          position: absolute;
          color: v.$white;
          width: 200%;
          left: 100%;
          transform: translate(-75%,2.5rem);
          @include m.tablet {
            left: 0;
            transform: unset;
            width: 70rem;
            top: 3.5rem;
          }
          @include m.desktop {
            top: 5.5rem;
          }
        }
      }
    }
    .bottom-area {
      color: v.$white;
      z-index: -1;
      height: auto;
      width: 100%;
      margin: auto;
      background-color: v.$primaryBlueDark;
      position: relative;
      top: -0.5rem;
      // top: 3.5rem;
      border-radius: 0 0 0 0;
      padding: 10% 2rem;
      text-align: center;
      @include m.tablet {
        padding: 7.5rem 2rem 4rem 2rem;
        width: 85%;
        top: -1rem;
        margin-left: 3rem;
        border-radius: 0 0 0 5rem;
        text-align: justify;
      }
      @include m.laptop {
        width: 72.5%;
        margin-left: 9rem;
        // top: 4.5rem;
      }
      @include m.desktop {
        width: 80%;
        // top: 6.5rem;
        border-radius: 0 0 0 10rem;
        padding: 12.5rem 5rem 5rem 7.5rem;
      }
      @include m.desktopXL {
        width: 70%;
        // top: 6.5rem;
        margin-left: 15%;
      }
      ul {
        li {
          list-style: disc;
          list-style-position: inside;
          margin-left: 2rem;
        }
      }
    }
    .service-head-logo {
      display: none;
      position: absolute;
      right: -7%;
      top: -7.5rem;
      // padding: 2rem;
      background-color: v.$grayBackground;
      width: 25rem;
      height: 25rem;
      border-radius: 50% 50% 0 0;
      @include m.tablet {
        padding-top: 1rem;
        display: flex;
        width: 12.5rem;
        height: 12.5rem;
        justify-content: center;
        align-items: center;
        // top: -2rem;
        // right: 3rem;
      }
      @include m.laptop {
        width: 12.5rem;
        height: 12.5rem;
        // right: 10rem;
      }
      @include m.desktop {
        top: -17rem;
        width: 25rem;
        height: 25rem;
        // right: 10rem;
      }
      @include m.desktopXL {
        top: -22.5rem;
        right: -2.5%;
        width: 30rem;
        height: 30rem;
        // right: 25%;
        // padding: 1rem 2rem 5rem 2rem;
      }
      img {
        height: 70%;
        @include m.tablet {
          // padding: 0 0.5rem 1rem 0.5rem;
        }
        @include m.laptop {
          // padding: 0 1rem 2rem 1rem;
        }
        @include m.desktop {
          // padding: 4rem 4rem 1rem 4rem;
        }
      }
    }
  }
}

.service-gallery {
  background-color: v.$white;
  width: 80%;
  height: auto;
  display: grid;
  grid-template-columns: 1fr;
  gap: 2rem;
  max-width: 120rem;
  margin: 0 auto 2rem auto;
  @include m.mobile {
    grid-template-columns: repeat(2, 1fr);
  }
  @include m.tablet {
    grid-template-columns: repeat(3, 1fr);
    margin-bottom: 7.5rem;
  }
  @include m.laptop {
    margin-bottom: 10rem;
  }
  .service-img {
    width: 100%;
    border: 1rem solid v.$grayBackground;
    img {
      width: 100%;
    }
  }
}
